/* Use font-face to import downloaded fonts and use within project */
/* Jakarta Regular */
@font-face {
  font-family: "Plus Jakarta Display Regular";
  src: local("PlusJakartaReg"),
    url("./fonts/PlusJakartaSans-Regular.woff2") format("woff2");
  font-weight: normal;
}

/* Jakarta Medium */
@font-face {
  font-family: "Plus Jakarta Display Medium";
  src: local("PlusJakartaMed"),
    url("./fonts/PlusJakartaSans-Medium.woff2") format("woff2");
  font-weight: normal;
}

* {
  font-family: "Plus Jakarta Display Medium", "Plus Jakarta Display Regular",
    Roboto, sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
